import {
  BarController, BarElement, CategoryScale, Chart, ChartData, ChartDataset, LinearScale,
} from 'chart.js';
import { Statement } from 'dashboard-types';

import { BarGraphReceivedMarksOptions } from '../types/bar-graph-received-marks-options';
/**
 * The BarGraphReceivedMarks is a graph type that displays the received marks of a student
 * based on the score verbs.
 */
export default class BarGraphReceivedMarks {
  /** The options for the graph. */
  #options: BarGraphReceivedMarksOptions;

  /** The statements to be displayed in the graph. */
  #statements: Statement[];

  /**
   *
   * @param statements - The statements to be displayed in the graph.
   * @param options - The options for the graph.
   */
  constructor(
    { statements, options }:
    Readonly<{ statements: Statement[]; options: BarGraphReceivedMarksOptions }>,
  ) {
    this.#statements = statements;
    this.#options = options;

    this.setDefaults();

    Chart.register(BarController, BarElement, CategoryScale, LinearScale);
  }

  /**
   * Draw the graph
   */
  public draw(): void {
    // eslint-disable-next-line no-new
    new Chart(this.#options.ctx, {
      type: 'bar',
      data: this.getChartData(),
      options: {
        responsive: true,
        maintainAspectRatio: false,
        events: [],
        scales: {
          x: {
            title: {
              display: true, // TODO: make dynamic
              text: this.#options.xAxisLabel,
            },
            stacked: true,
            ticks: {
              autoSkip: false,
            },
          },
          y: {
            title: {
              display: true,
              text: this.#options.yAxisLabel,
            },
            beginAtZero: true,
            position: 'left',
            min: 0,
            max: 100,
          },
          y2: {
            display: this.#options.graphFitInfoBar,
            title: {
              display: true,
              text: this.#options.y2AxisLabel,
            },
            beginAtZero: true,
            position: 'right',
            min: 0,
            max: 100,
          },
        },
      },
    });
  }

  /**
   * Set the default values for the options
   */
  private setDefaults(): void {
    this.#options.scoreRange = this.#options.scoreRange ?? [0, 100];
    this.#options.showAvg = this.#options.showAvg ?? false;
    this.#options.showAvgLine = this.#options.showAvgLine ?? false;
    this.#options.xAxisLabel = this.#options.xAxisLabel ?? 'Score Range [%]';
    this.#options.yAxisLabel = this.#options.yAxisLabel ?? '% of Group';
    this.#options.y2AxisLabel = this.#options.y2AxisLabel ?? 'Score [%]';
    this.#options.nrOfBars = this.#options.nrOfBars ?? 10;
    this.#options.barColors = this.#options.barColors ?? [
      '#FF6384',
      '#4BC0C0',
      '#FFCD56',
      '#C9CBCF',
      '#36A2EB',
      '#B284BE',
    ];
    this.#options.graphFitInfoBar = this.#options.graphFitInfoBar ?? false;
    this.#options.followBlock = this.#options.followBlock ?? false;
    this.#options.averageScoreBarLabel = this.#options.averageScoreBarLabel ?? 'Average in this group';
    this.#options.showScore = this.#options.showScore ?? false;
    this.#options.groupScoreLabel = this.#options.groupScoreLabel ?? 'avg score';
    this.#options.showNumberOfParticipants = this.#options.showNumberOfParticipants ?? false;
    this.#options.groupUsersLabel = this.#options.groupUsersLabel ?? 'participants in group';
    this.#options.ownScoreLabel = this.#options.ownScoreLabel ?? 'Your score';
    this.#options.userScoreLabel = this.#options.userScoreLabel ?? 'User score';
  }

  /**
   * Get the chart data
   */
  private getChartData(): ChartData {
    const {
      datasets, groupAverage, participantsPerGroup, ownScoreLabel,
    } = this.getDatasets();
    const labels = this.getLabels({ groupAverage, participantsPerGroup, ownScoreLabel });

    return {
      datasets,
      labels,
    };
  }

  /**
   * Get the labels for the chart
   *
   * @param {[number, number][]} groupAverage - The average score for each group
   * @param {number[]} participantsPerGroup - The number of participants in each group
   * @param {[number, number]} ownScoreLabel - The label for the own score and where to place it
   * @returns The labels for the chart
   */
  private getLabels(
    { groupAverage, participantsPerGroup, ownScoreLabel }:
    Readonly<{
      groupAverage?: [number, number][];
      participantsPerGroup?: number[];
      ownScoreLabel?: [number, number];
    }>,
  ): string[][] {
    if (this.#options.nrOfBars === undefined) {
      throw new Error('nrOfBars is not defined');
    }

    const labels = [];

    // Add the labels for the bars, [0-10, 10-20, etc]
    if (this.#options.followBlock) {
      // TODO: set labels when follow block
    } else {
      const step = Math.ceil(100 / this.#options.nrOfBars);
      for (let i = 0; i < 100; i += step) {
        labels.push([`${i}-${i + step}`]);
      }
    }

    // Add the average score to each label for the bars
    if (this.#options.showAvg) {
      if (groupAverage === undefined) {
        throw new Error('groupAverage is not defined');
      }
      labels.map((label, i) => label.push(`${this.#options.groupScoreLabel}: ${groupAverage[i][1].toFixed(2)}`));
    }

    // Show the score for the current user
    if (this.#options.showScore && this.#options.userStatement) {
      if (ownScoreLabel !== undefined) {
        const [index, labelScore] = ownScoreLabel;
        labels[index].push(`${this.#options.userScoreLabel}: ${labelScore}`);
      }
    }

    // Add the number of participants to each label for the bars
    if (this.#options.showNumberOfParticipants) {
      labels.map((label, i) => label.push(`${participantsPerGroup?.[i] ?? 0} ${this.#options.groupUsersLabel}`));
    }

    return labels;
  }

  /**
   * Get the datasets for the graph
   *
   * @returns The datasets for the graph, the group average data, the
   * users own score, and the number of participants per group
   */
  private getDatasets(): {
    datasets: ChartDataset[];
    groupAverage?: [number, number][];
    ownScore?: [number, number][];
    ownScoreLabel?: [number, number];
    participantsPerGroup?: number[];
  } {
    const datasets: ChartDataset[] = [];
    const backgroundColor = [...this.#options.barColors ?? []];

    let groupAverage;
    let ownScore;
    let ownScoreLabel;
    let borderColor = [...this.#options.barColors ?? []];

    if (this.#options.showAvgLine && this.#options.showAvg) {
      const {
        ownScore: extractedOwenScore,
        ownScoreLabel: extractedOwnScoreLabel,
        borderColor: extractedBorderColor,
      } = this.getOwnScore();
      ownScore = extractedOwenScore;
      ownScoreLabel = extractedOwnScoreLabel;
      borderColor = extractedBorderColor ?? [];

      groupAverage = this.getGroupAverage();

      datasets.push({
        type: 'bar',
        label: this.#options.ownScoreLabel,
        data: ownScore,
        backgroundColor: '#000000',
      });
      datasets.push({
        type: 'bar',
        label: this.#options.averageScoreBarLabel,
        data: groupAverage,
        backgroundColor: '#8f4e4e',
      });
    }

    const { data, participantsPerGroup } = this.getData();
    datasets.push({
      type: 'bar',
      data,
      backgroundColor,
      borderWidth: 5,
      borderColor,
    });

    return {
      datasets, groupAverage, ownScore, ownScoreLabel, participantsPerGroup,
    };
  }

  /**
   * Get the data for the chart
   *
   * @returns The data for the chart, and the participantsPerGroup counts per score range
   */
  private getData(): { data: number[], participantsPerGroup: number[] } {
    if (!this.#options.nrOfBars) {
      throw new Error('nrOfBars is not defined');
    }

    // Initialize the data array with zeros
    const data = Array.from({ length: this.#options.nrOfBars }, () => 0);

    // Count the number of statements for each score range
    this.#statements.forEach((statement) => {
      const index = this.getIndex(statement.result?.score?.raw ?? 0);
      data[index] += 1;
    });

    const participantsPerGroup = [...data];

    // Convert to percentage if the statements array is not empty
    if (this.#statements.length !== 0) {
      return {
        data: data.map((dataPoint) => (dataPoint / this.#statements.length) * 100),
        participantsPerGroup,
      };
    }

    return { data, participantsPerGroup };
  }

  /**
   * Get the average data of the group
   *
   * @returns The average data of the group
   */
  private getGroupAverage(): [number, number][] {
    if (!this.#options.nrOfBars) {
      throw new Error('nrOfBars is not defined');
    }

    // Initialize the data array with zeros
    const groupData = Array.from(
      { length: this.#options.nrOfBars },
      () => ({ sum: 0, count: 0 }),
    );

    // Count the number of statements for each score range
    this.#statements.forEach((statement) => {
      const rawScore = statement.result?.score?.raw ?? 0;
      const index = this.getIndex(rawScore);
      groupData[index].sum += rawScore;
      groupData[index].count += 1;
    });

    // Compute the average score for each group
    const groupAverage: [number, number][] = groupData.map((data) => {
      const avg = data.count > 0 ? (data.sum / Math.max(data.count, 1)) : 0;
      return [avg - 1, avg];
    });

    if (this.#options.graphFitInfoBar) {
      // TODO: implement scaling to bar
    }

    return groupAverage;
  }

  /**
   * Get the score data of the current user
   *
   * @returns The score data of the current user
   */
  private getOwnScore(): {
    ownScore: [number, number][];
    ownScoreLabel: [number, number] | undefined;
    borderColor: string[] | undefined;
  } {
    if (!this.#options.nrOfBars) {
      throw new Error('nrOfBars is not defined');
    }
    if (!this.#options.userScore || !this.#options.userStatement) {
      return { ownScore: [], ownScoreLabel: undefined, borderColor: this.#options.barColors };
    }

    // Initialize the data array with zeros
    const ownScore: [number, number][] = Array.from(
      { length: this.#options.nrOfBars },
      () => [0, 0],
    );

    let ownScoreLabel: [number, number] | undefined;
    const borderColor = this.#options.barColors;

    // Count the number of statements for each score range
    this.#statements.forEach((statement) => {
      if (statement === this.#options.userStatement) {
        const rawScore = statement.result?.score?.raw ?? 0;
        const index = this.getIndex(rawScore);
        ownScore[index] = [this.#options.userScore! - 1, this.#options.userScore!];
        ownScoreLabel = [index, this.#options.userScore!];
        if (borderColor !== undefined) { borderColor[index] = '#484848'; }
      }
    });

    return { ownScore, ownScoreLabel, borderColor };
  }

  /**
   * Get the index for the chart data
   *
   * @param score - The score to get the index for
   * @returns The index for the chart data
   */
  private getIndex(score: number): number {
    if (!this.#options.nrOfBars) {
      throw new Error('nrOfBars is not defined');
    }

    const index = Math.floor(
      (score / Math.ceil(100 / this.#options.nrOfBars!)),
    );

    return index >= this.#options.nrOfBars ? this.#options.nrOfBars - 1 : index;
  }
}
